/* CSS Styles */
body {
    background-color: #c21010;
  }
  
  .container {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 5vh;
    
    background-size: cover;
  }
  
  .paper {
    padding: 40px;
    width: 400px;
    background-color: white;
    box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.2);
  }
  
  h2 {
    margin-bottom: 20px;
  }
  
  .input-field {
    margin-bottom: 20px;
  }
  
  .login-btn {
    width: 100%;
    margin-bottom: 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px;
    cursor: pointer;
  }
  
  .register-btn {
    width: 100%;
    margin-bottom: 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px;
    cursor: pointer;
  }
  
  .google-btn {
    width: 100%;
    margin-bottom: 20px;
    background-color: #ffffff;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px;
    cursor: pointer;
  }
  
  .apple-btn {
    width: 100%;
    background-color: #000000;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 10px;
    cursor: pointer;
  }
  
  .forgot-password {
    margin-top: 10px;
    color: #007bff;
  }
  
  /* Media Queries */
  @media (max-width: 600px) {
    .paper {
      width: 100%;
    }
  }